* {
    box-sizing: border-box;
    margin: 0;
    bottom: 0;
}
.lsts{
    position: fixed;
    top: 75px;right: 10px;left: 10px;bottom: 10px;
    border-radius:10px;
    background: #F2F4F8;
    overflow: scroll;
     
}

.me {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 1px solid #0F62FE;
    margin-top: 20px;
}

body {
    background: #d8d8d8 !important;
}

.fa-brands {
    font-size: 36px;
    display: block;
    margin-top: 20px;
}

.skilscont {
    background: rgb(209, 209, 209);
    width: 100px;
    height: 100px;
    margin: 5px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    display: inline-block;
    border-radius: 50%;
    border-bottom: 2px solid #0F62FE;
    border-top: 2px solid #0F62FE;
    border-left: 3px solid rgb(0, 0, 0);
    border-right: 3px solid rgb(0, 0, 0);
    transition: 0.3s;
}

.skilscont:hover {
    width: 100px;
    height: 100px;
    border-bottom: 2px solid rgb(0, 0, 0);
    border-top: 2px solid rgb(0, 0, 0);

    border-left: 3px solid #0F62FE;
    border-right: 3px solid #0F62FE;
  transition: 0.3s;
    background: rgb(175, 175, 175);
    color: #ffffff;
    text-shadow: 0 0 25px #ffffff;
}

.data1 h2 {
    -webkit-text-stroke: 0.2px #0F62FE;
}

.fc {
    text-decoration: none;
}



.blb {
    width: 100%;

}

.blb img {
    width: 300px;
    margin: 50px 0;
}
.fccont{
width:40px;
height:40px;
margin:20px;
display: inline-block;
}
::-webkit-scrollbar {
    width:0px;
    height:0px;
    
   }
   
  ::-webkit-scrollbar-track {
   
    border: 7px solid #000000;
    box-shadow: inset 0 0 2.5px 2px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    }
  ::-webkit-scrollbar-thumb {
    background: linear-gradient(90deg, #0F62FE, #000103);
    border-radius: 10px; 
}
   

@media (max-width:350px) {
    .blb img{
        width:250px;
    }
    .fccont{
        width:40px;
        height:40px;
        margin:10px;
        display: inline-block;
        }
}
@media (max-width:280px) {
    .blb img{
        width:200px;
    }
    .fccont{
        width:40px;
        height:40px;
        margin:5px;
        display: inline-block;
        }
}









