.inps{

  height:150px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-basis:25%;
  border:1px solid rgb(0, 0, 0);
  width:250px;
  margin:50px auto;
  border-radius:10px;
  background: rgb(255, 255, 255);

}
.inps input{
  margin:5px 0;
  border-radius:10px;
  border:none;
  outline:1px solid #000;
}
.inps input{
  margin:5px 0;
  border-radius:10px;
  border:1px solid rgb(17, 0, 255);
  outline: none;
}
.inps input:focus{
  margin:5px 0;
  border-radius:10px;
  border:none;
  outline:1px solid #000;
}
.inps button{
  width:80px;
  border-radius:8px;
  border:1px solid #000;
}
.inps button:hover{
  background: rgb(153, 153, 153);
}
.btn{
  margin-left:10px;
}
