#app {
  height: 100%
}

html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}

.sldr {

  margin-top: 200px;
 width:90%;
margin: auto;
}
h1::selection{

  background:none;
  color:none;
}
h4::selection{  background:none;color:none;}
p::selection{  background:none;color:none;}
a::selection{  background:none;color:none;}
img::selection{  background:none;color:none;}
.crd {
  height: 450px;
  border-radius: 7px;
  border: 1px solid #000;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.crd a {
  text-decoration: none;
  background: rgb(182, 182, 182);
  padding: 8px 30px;
  border-radius: 7px;
  color: rgb(0, 0, 0);
}

.crd a:hover {
  background: rgb(143, 142, 142);
}

.crd img {

  border-radius:5px 5px 0 0 ;
  width:100% !important;
  height: 240px;
  border-bottom:1px solid #000;
}

h1 {
  text-align: center;
}
@media (max-width:500px) {
  .sldr {

    margin-top: 200px;
   width:100%;
  margin: auto;
  }
  .crd {
    height: 300px;
    /* width: 170.5px !important; */
    border-radius: 7px;
    border: 1px solid #000;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  .crd a {
    text-decoration: none;
    background: rgb(182, 182, 182);
    padding: 2px 10px;
    border-radius: 7px;
    color: rgb(0, 0, 0);
  }
}